const PropertySale = (props) => {
  const [activeTab, setActiveTab] = useState('details');
  const [modalOpen, setModalOpen] = useState(false);
  const [userData, setUserData] = useState({});
  const [userId, setUserId] = useState('');
  const [userName, setUserName] = useState('');
  const [userImage, setUserImage] = useState('');
  const [modal, setModal] = useState(false);
  const [loadingUser, setloadingUser] = useState(true);
  const [propertyData, setPropertyData] = useState([]);

  const [role, setRole] = useState('');
  const [loader, setloader] = useState(true);

  useEffect(() => {
    check();
    getProperties();
  }, []);

  const check = () => {
    let user = reactLocalStorage.get('user_data');
    if (user) {
      user = JSON.parse(user);
      console.log('user>>>>', user);
      setRole(user.role);
    }
  };

  const getProperties = () => {
    new ApiManager().getProperties().then((result) => {
      if (result.no_result) {
        return;
      }
      if (result.data) {
        if (result.data.error) {
          alert(result.data.error);
          return;
        }
        if (result.data) {
          let arr = result.data.list;
          arr = arr.filter((el) => el.propertyType === 'sale');
          setPropertyData(arr);
          setloader(false);
        }
        console.log('result user data>>>', result);
      }
    });
  };

  const ModalExample = (props) => {
    const toggle = () => setModal(!modal);
    return (
      <div>
        <Modal isOpen={modal} toggle={toggle}>
          <ModalHeader toggle={toggle}>Subscribe</ModalHeader>
          <ModalBody>
            This is dummy text later on will be add real body text...
          </ModalBody>
          <ModalFooter>
            <Button color="secondary" onClick={toggle}>
              Cancel
            </Button>
            <Button
              color="primary"
              onClick={() => {
                setModal(false);
                // followAdd();
              }}
            >
              Subscribe
            </Button>{' '}
          </ModalFooter>
        </Modal>
      </div>
    );
  };

  return (
    <>
      <AppLayout>
        {/* {ModalExample()} */}
        <Row>
          <Colxx xxs="12">
            <br />
            <div
              style={{
                textAlign: 'center',
                width: '100%',
              }}
            >
              <h1>Properties for Sale</h1>
              {/* {Carousel()} */}
              <div style={{ width: '100%', textAlign: 'right' }}>
                {role === 'admin' ? (
                  <Button
                    style={{
                      marginBottom: '10px',
                    }}
                    color="primary"
                    className=""
                    onClick={() => {
                      props.history.push('/properties/register');
                    }}
                  >
                    Add Property
                  </Button>
                ) : undefined}
              </div>
            </div>
          </Colxx>
          <br />
          {!loader ? (
            propertyData.length >= 1 ? (
              propertyData &&
              propertyData.map((item, i) => {
                return (
                  <Colxx xxs="12" lg="4" className="mb-5" key={i}>
                    <Card
                      style={{ height: '' }}
                      className="mb-4"
                      onClick={() => {
                        props.history.push('/properties/property-sale/detail', {
                          propData: item,
                        });
                      }}
                    >
                      <div className="position-absolute card-top-buttons"></div>
                      <SingleLightbox
                        thumb={item.propertyMainImage}
                        large={item.propertyMainImage}
                        className="card-img-top"
                        height="250px"
                      />
                      <CardBody>
                        <h2 className="text-muted  mb-2">
                          {item.propertyStatus}
                        </h2>
                        <footer style={{ textAlign: 'left' }}>
                          <p className="text-muted text-small mb-0 font-weight-light">
                            {' '}
                            {item.propertyDescription.substring(0, 100)}
                          </p>
                          <p>
                            BuiltIn{' '}
                            {item.propertyBuiltIn
                              ? moment(item.propertyBuiltIn).format('LL')
                              : undefined}
                          </p>
                        </footer>
                      </CardBody>
                    </Card>
                  </Colxx>
                );
              })
            ) : (
              'No Property Added Yet...'
            )
          ) : (
            <div
              style={{
                width: '100%',
                textAlign: 'center',
                marginTop: '50px',
              }}
            >
              <Spinner color="primary" animation="border" role="status">
                <span className="sr-only">Loading...</span>
              </Spinner>
            </div>
          )}
          <br />
          <br />
        </Row>
        <br />
      </AppLayout>
    </>
  );
};
