function Project(props) {
  return (
    <CardMedia
      className="Media"
      image={props.item.image}
      style={{ height: '600px' }}
    >
      <CardContent>
        <div
          style={{
            marginTop: '300px',
            marginLeft: '100px',
            padding: '50px',
            width: '800px',
            height: '200px',
            background: '#32423c82',
          }}
        >
          <Typography
            style={{
              fontSize: '50px',
              color: 'white',
            }}
          >
            {props.item.txt}
          </Typography>
        </div>
      </CardContent>
    </CardMedia>
  );
}

