import React, { Component } from 'react';
import { render } from 'react-dom';
import GoogleMapReact from 'google-map-react';
// import './style.css';
import marker from './carousel/images/marker.png'

export default class Location extends Component {

    constructor(props) {
        super(props);

        this.state = {

            //This is where the center of map is going to be
            center: {
               
                lat: 33.714642,
                lng: 73.066174
            },

            //This is how much you want to zoom in
            zoom: 16,

            //This is the list of markers.
            myMarkers: []
        };


        //Adding to the list of markers
        const aMarker = {
            name: 'office',
            lat: 33.714642,
            lng: 73.066174
        }
        this.state.myMarkers.push(aMarker);
        this.setState({ myMarkers: this.state.myMarkers });
    }

   

    
    render() {
        let h;
        if(this.props.height){
            h = this.props.height
        }else{
            h="64vh"
        }
        //Marker Component
        const Marker = ({ text }) => {
            return (
                <div>
                    <img
                        style={{ width: '40px', height: '40px' }}
                        src={marker}
                        alt={text} />
                    <b style={{ fontSize: '15px' }}>{text}</b>
                </div>
            );
        }

        return (
            <div>
                <center>
                    <div style={{ height: h, width: '100%' }}>

                        <GoogleMapReact
                            bootstrapURLKeys={{ key: "AIzaSyAR8yDgl9BStuvSIEehKKNY2XM42dr8nUY" }}

                            defaultCenter={this.state.center}
                            defaultZoom={this.state.zoom}
                        >


                            {
                                //Add a list of Markers to Your Map
                                this.state.myMarkers.map((each, i) =>
                                    <Marker
                                        key={i}
                                        lat={each.lat}
                                        lng={each.lng}
                                        text={each.name}
                                    />
                                )
                            }
                        </GoogleMapReact>
                    </div>
                </center>
            </div>

        );
    }
}
